<template>
  <div :class="animateClass">
    <searchnav title="选择城市"></searchnav>
    <van-search v-model="value"
                background="#4fc08d"
                label='城市'
                @input="get"
                placeholder="请输入搜索关键词">
    </van-search>

    <van-index-bar @select='change'
                   :index-list='[]'>
      <!-- 这里空数组是取消侧边字母提示 -->
      <div v-for="(item, i) in cList"
           :key="i">
        <van-index-anchor :index="item" />
        <van-cell v-for="(C, i) in cities.filter(p=>p.pinyin.indexOf(item.toLowerCase())==0)"
                  :key="i"
                  :title="C.name"
                  @click="link(C)" />
      </div>
    </van-index-bar>
  </div>
</template>
<script>

import { Toast } from 'vant'
import { mixin } from '@/tools'
export default {
  mixins: [mixin],//利用混入，和路由守卫，给每个路由添加切换动画效果。
  //混入主要用来vue组件中的可复用功能，可以包含任意组件选项。
  //混入：mixin可以看做一个可以随时使用的没有template的组件，而且不用全局声明，除了没有template，其他的data，method，watch，computed，beforeRouterEnter，beforeRouterLeave，路由守卫等它都有。
  //混入注意点由两个，一个是混入的同名组件选项，会被组件自身选项覆盖，
  //                二个是混入的同名钩子函数，会合并成一个数组，且混入的钩子函数先被调用。
  //                三个是值是对象选项，如methods，components，directives将被合并为一个对象，键名冲突取组件的。
  //总结：除了钩子函数都会被调用外，其他同名以组件自身为重。
  data () {
    return {
      cList: [],
      cities: [],
      value: "",
    }
  },
  methods: {
    link (city) {
      localStorage.setItem("city", JSON.stringify(city))
      this.$router.push({ name: 'cinemalist' })
    },
    change (data) {
    },
    get () {
      console.log(this.value)
      this.axios({
        url: '/zuo/gateway?k=176893',
        headers: {
          "X-Client-Info": '{"a":"3000","ch":"1002","v":"5.0.4","e":"15881617152546915606890","bc":"110100"}',
          "X-Host": "mall.film-ticket.city.list"
        }
      }).then(res => {
        this.cities = res.data.data.cities
        this.cities = this.cities.filter(item => {
          return item.name.indexOf(this.value) == 0
        })
        this.cList = new Set(this.cities.map(item => item.pinyin.slice(0, 1).toUpperCase()))//这里是利用了set去重
      })
    }
  },
  mounted () {
    Toast.loading({
      message: '加载中...',
      forbidClick: true,
      duration: 2000,
      overlay: true
    });
    this.axios({
      url: '/zuo/gateway?k=176893',
      headers: {
        "X-Client-Info": '{"a":"3000","ch":"1002","v":"5.0.4","e":"15881617152546915606890","bc":"110100"}',
        "X-Host": "mall.film-ticket.city.list"
      }
    }).then(res => {
      this.cList = ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'J', 'K', 'L', 'M', 'N', 'O', 'P', 'Q', 'R', 'S', 'T', 'U', 'V', 'W', 'X', 'Y', 'Z']
      // Toast.clear()
      res ? Toast({
        type: 'success',
        icon: require('@/assets/imgs/ajax-loader.gif'),
        message: '加载成功',
        overlay: true,
        closeOnClickOverlay: true
      }) : Toast.fail('加载失败');
      this.cities = res.data.data.cities
    })
  },
}
</script>
<style lang="scss" scoped >
</style>